import { isValidABN, isValidACN } from 'abnacn-validator';
import { Autocomplete, createFilterOptions, TextField } from '@material-ui/core';
import styled from '@emotion/styled';
import { css } from 'emotion';
import React from 'react';
import Company from 'types/Company';
import { noSpaces } from 'utils/string';
import { formatAbnAcn } from 'utils/abnAcn';

const Name = styled.span`
  display: block !important;
  font-size: 14px;
  margin-bottom: 0.5em;
`;

const EntityMatch = styled.span`
  display: block;
`;

const Abn = styled.span`
  display: block;
  font-size: 11px;
  color: #666;
`;

const ListBoxStyles = css`
  .MuiAutocomplete-option {
    padding-top: 8px;
    padding-bottom: 8px;
  }
`;

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

export interface CompanyInput extends Company {
  inputValue?: string;
}

const allCompanyEntities: CompanyInput[] = [
  {
    abn: '86162372741',
    acn: '162372741',
    name: 'APOSITIVE PTY LTD'
  },
  {
    acn: '609328207',
    name: 'CROFT FAMILY PTY LTD'
  }
];

const filter = createFilterOptions<CompanyInput>();

interface CompanyPickerProps {
  value: CompanyInput | null;
  onChange: (tradingEntity: CompanyInput | null) => void;
}

const CompanyPicker = ({ value, onChange }: CompanyPickerProps) => {
  return (
    <Autocomplete
      value={value}
      autoHighlight
      classes={{
        listbox: ListBoxStyles
      }}
      options={allCompanyEntities}
      getOptionSelected={(option: CompanyInput, value: CompanyInput) => { 
        return value ? option === value : false;
      }}
      renderOption={(props, value, state) => {
        return (
          // value.abn === '' && !value.inputValue ? 
          // <span>
          //   <EntityMatch>
          //     <Abn>{formatAbnAcn(value.acn)} {!!value.abn && <> / {formatAbnAcn(value.abn)}</>}</Abn>
          //   </EntityMatch>
          // </span> :
          <li {...props}>
            <EntityMatch>
              <Name>{value.name}</Name>
              {!value.inputValue && <Abn>{formatAbnAcn(value.acn)} {!!value.abn && <> / {formatAbnAcn(value.abn)}</>}</Abn>}
            </EntityMatch>
          </li>
        )
      }}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === 'string') {
          return option;
        }
        // Add "xxx" option created dynamically
        if (option.inputValue) {
          return option.inputValue;
        }
        // Regular option
        return `${option.name} ${option.acn.replace(/\s/g, '')} ${option.abn?.replace(/\s/g, '')}`
      }}
      // getOptionLabel={(option) => `${option.name} ${option.email} ${option.mobile}`}
      // value={value}
      // value={selectedValueWithLabel}
      // onChange={(evt, newValue) => onChange(newValue)}
      onChange={(evt, newValue) => {
        if (typeof newValue === 'string') {
          onChange({
            acn: '',
            name: newValue,
            // email: newValue,
          });
        } else if (newValue) {
          // Create a new value from the user input
          onChange(newValue);
        } else {
          onChange(null);
        }
      }}
      filterOptions={(options, params) => {
        const filtered = filter(options, params);
        const cleanValue = noSpaces(params.inputValue);
        // const numericValue = parseInt(cleanValue)
        // const isNumeric = `${numericValue}`.length === cleanValue.length && !isNaN(numericValue);

        // Suggest the creation of a new value
        if (cleanValue !== '') {
          // if (isNumeric) {
          if ((cleanValue.length === 9 && isValidACN(cleanValue)) || (cleanValue.length === 11 && isValidABN(cleanValue))) {
            filtered.push({
              inputValue: params.inputValue,
              acn: cleanValue,
              name: `Create Company "${formatAbnAcn(cleanValue)}"`
              // email: `Add "${params.inputValue}"`
            });
            // }
          } else {
            filtered.push({
              inputValue: params.inputValue,
              acn: '',
              name: `Create Company "${params.inputValue}"`
            });
          }
        }

        return filtered;
      }}
      // onBlur={onBlur}
      // freeSolo
      renderInput={({ InputLabelProps, InputProps, ...rest }) => 
        <TextField
          autoFocus={true}
          className={TextFieldStyle}
          // label="Find or create user"
          // error={!!error}
          InputLabelProps={{ shrink: true, ...InputLabelProps }}
          InputProps={{ placeholder: 'Find by ACN, ABN or name', ...InputProps }}
          // helperText={ error?.message ?? '' }
          {...rest}
        />
      }
    />
  )
};

export default CompanyPicker;
