import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, useMediaQuery, useTheme } from '@material-ui/core';
import { css } from 'emotion';
import { useCompanyContext } from 'providers/Company';
import React, { useEffect } from 'react';
import { useState } from 'react';
import { bp } from 'theme';
import CompanyPicker, { CompanyInput } from './CompanyPicker';
import NewCompanyDetails, { NewCompanyDetailsForm, NewCompanyDetailsRefProps } from './NewCompanyDetails';
import { MdWarning } from "react-icons/md";

const ContentStyle = css`
  ${bp.xs} {
    width: 100%
  }
  ${bp.sm} {
    width: 580px;
  }
`

// const SelectedEntityChipLabelStyle = css`
//   font-size: 14px;
//   ${bp.xs} {
//     max-width: 340px;
//   }
//   ${bp.sm} {
//     max-width: 500px;
//   }
// `

export interface AddCompanyTrusteeDialogProps {
  isOpen: boolean;
  // company?: CompanyInput;
  onClose: () => void;
  onSubmit: (newCompanyDetails: NewCompanyDetailsForm) => void;
}

function AddCompanyTrusteeDialog({ isOpen, onClose, onSubmit }: AddCompanyTrusteeDialogProps) {
  const [selectedCompany, setSelectedCompany] = useState<CompanyInput | null>(null);
  const [isDirty, setIsDirty] = useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xlMob'))
  const { company, setCompany } = useCompanyContext();
  const newCompanyDetailsRef = React.useRef<NewCompanyDetailsRefProps>(null);

  useEffect(
    () => {
      // Unmount CallBack
      if (!isOpen) {
        reset();
      }
      return () => reset();
    },
    [isOpen]
  )

  useEffect(
    () => {
      setCompany(selectedCompany ? !!selectedCompany.inputValue ?
        selectedCompany.acn ? {
          name: '',
          acn: selectedCompany.acn
        } : {
          name: selectedCompany.inputValue,
          acn: ''
        } : selectedCompany : undefined)

    },
    [selectedCompany, setCompany]
  )

  function reset() {
    setSelectedCompany(null);
  }

  function onModalClose() {
    onClose();
  }

  function onModalOK() {
    if (newCompanyDetailsRef.current) {
      onSubmit(newCompanyDetailsRef.current.getFormValues())
    };
  }

  // function onSelectionRemove() {
  //   setSelectedCompany(null);
  // }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        Add Company Trustee { isDirty && <MdWarning /> }
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            {
              (company) ?
              <NewCompanyDetails
                ref={newCompanyDetailsRef}
                initialCompany={company}
                onDirtyChange={setIsDirty}
              />
              :
              <CompanyPicker
                value={selectedCompany}
                onChange={setSelectedCompany}
              />
            }
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" disabled={!selectedCompany} onClick={onModalOK}>
          Add
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default AddCompanyTrusteeDialog;
