import styled from "@emotion/styled";
import { Button, FormLabel, Grid } from "@material-ui/core";
import SwitchField from "components/form/SwitchField";
import TextField from "components/form/TextField";
import SectionHeading from "components/SectionHeading";
import { css } from "emotion";
import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { MdAdd, MdPerson } from "react-icons/md";
import Director from "types/Director";
import Company from "types/Company";
import { formatAbnAcn } from "utils/abnAcn";
import CompanyDirectorDetails from "./CompanyDirectorDetails";
import LazyDialog from "components/LazyDialog";
import { DirectorForm } from "./DirectorFormPanel";

const AddDirectorDialog = React.lazy(() => import('./AddDirectorDialog'))

const Directors = styled.div`
`;

const Header = styled.div`
  display: flex;
`;

const LabelStyle = css`
  flex-grow: 1;
  font-size: 11px;
  color: #777;
  line-height: 2em;
  align-self: flex-end;
`;

const Actions = styled.div`
  .MuiButton-root {
    margin-left: 8px;
  }
`;

const EmptyState = styled.div`
  background-color: #eee;
  padding: 24px;
  text-align: center;
`;

export interface NewCompanyDetailsProps {
  initialCompany: Company;
  onDirtyChange: (isDirty: boolean) => void;
  // onChange: (tradingEntity?: TradingEntity) => void;
}

export interface NewCompanyDetailsForm {
  Name: string;
  Acn: string;
  Abn: string;
  IsTradingEntity: boolean;
  IsForeign: boolean;
  ForeignIdentifier: string;
  Directors: Director[]; // Not used in Form Fields
}

export interface NewCompanyDetailsRefProps {
  getFormValues: () => NewCompanyDetailsForm;
}

function NewCompanyDetails({ initialCompany: { acn, abn, name }, onDirtyChange }: NewCompanyDetailsProps, ref: React.Ref<NewCompanyDetailsRefProps>) {
  const [isDirectorModalOpen, setIsDirectorModalOpen] = useState(false);
  const [directors, setDirectors] = useState<Director[]>([]);

  React.useImperativeHandle(ref, () => ({ getFormValues }));

  function getFormValues(): NewCompanyDetailsForm {
    return { ...getValues(), Directors: directors };
  };

  const methods = useForm<NewCompanyDetailsForm>({
    defaultValues: {
      Name: name,
      Acn: formatAbnAcn(acn),
      IsTradingEntity: !!abn,
      Abn: formatAbnAcn(abn),
      IsForeign: false,
    },
    mode: 'onChange',
  });

  const {
    control,
    watch,
    formState: { isDirty },
    getValues,
  } = methods;

  const isTradingEntity = watch('IsTradingEntity');
  const isForeign = watch('IsForeign');

  useEffect(
    () => {
      onDirtyChange(isDirty);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [isDirty],
  );

  function onDirectorModalClose() {
    setIsDirectorModalOpen(false);
  }

  function onDirectorModalSubmit(directorForm: DirectorForm) {
    setIsDirectorModalOpen(false);
    addDirector({
      firstGivenName: directorForm.FirstGivenName,
      otherGivenName: directorForm.OtherGivenName,
      familyName: directorForm.FamilyName,
      ceaseDate: directorForm.CeaseDate || undefined,
      appointmentDate: directorForm.AppointmentDate || undefined,
    });
  }

  function onAddDirector() {
    setIsDirectorModalOpen(true);
  }

  function addDirector(director: Director) {
    setDirectors([...directors, director]);
  }

  function removeDirector(index: number) {
    setDirectors(directors.slice(0, index).concat(directors.slice(index + 1)));
  }
  return (
    <>
      <Grid container spacing={2} alignItems="flex-end">
        <Grid item xs={12}><SectionHeading>Company Details</SectionHeading></Grid>
        <Grid item xs={10}>
          <TextField
            autoFocus
            id="NewCompanyName"
            name="Name"
            label="Name"
            control={control}
          />
        </Grid>
        <Grid item xs={2}>
          <SwitchField
            name="IsForeign"
            control={control}
            label="Foreign?"
          />
        </Grid>
        {
          isForeign ?
          <Grid item xs={12}>
            <TextField
              name="ForeignIdentifier"
              label="Identifier"
              control={control}
            />
          </Grid>
          :
          <>
            <Grid item xs={12} md={3}>
              <TextField
                name="Acn"
                label="ACN"
                control={control}
              />
            </Grid>
            <Grid item xs={3}>
              <SwitchField
                name="IsTradingEntity"
                control={control}
                label="Is Trading?"
              />
            </Grid>
            {
              isTradingEntity &&
              <Grid item xs={9} md={6}>
                <TextField
                  name="Abn"
                  label="ABN"
                  control={control}
                />
              </Grid>
            }
          </>
        }
        <Grid item xs={12}>
          <Directors>
            <Header>
              <FormLabel className={LabelStyle}>Directors</FormLabel>
              <Actions>
                <Button
                  startIcon={<MdAdd />}
                  endIcon={<MdPerson/>}
                  onClick={onAddDirector}
                />
              </Actions>
            </Header>
            {
              !directors?.length ?
              <EmptyState>No directors assigned</EmptyState>
              :
              directors.map((director, index) =>
                <CompanyDirectorDetails
                  key={index}
                  index={index}
                  director={director}
                  onRemove={removeDirector} />
              )
            }
          </Directors>
        </Grid>
      </Grid>
      <LazyDialog isOpen={isDirectorModalOpen}>
        <AddDirectorDialog
          isOpen={isDirectorModalOpen}
          onClose={onDirectorModalClose}
          onSubmit={onDirectorModalSubmit}
        />
      </LazyDialog>
    </>
  )
}

export default React.forwardRef(NewCompanyDetails);
