import styled from "@emotion/styled";
import React from "react";
import { MdMoreVert, MdPerson } from "react-icons/md";
import { Grid, IconButton, Menu, MenuItem } from "@material-ui/core";
import { css } from "emotion";
import Director from "types/Director";
import DisplayField from "components/form/DisplayField";
import { formatDate } from "utils/date";

const DirectorRoot = styled.div`
  background-color: #eee;
  margin-bottom: 2px;
`;

const Head = styled.div`
  display: flex;
`;

const Body = styled.div`
  padding: 0 16px 16px 46px;
  display: flex;
`;

const Title = styled.div`
  flex-grow: 1;
  padding: 16px;
  display: flex;
`;

const CoreDetails = styled.div`
  flex-grow: 1;
`;

const Name = styled.span`
  display: inline-block;
  font-size: 14px;
`;

const Icon = styled.span`
  display: inline-block;
  font-size: 16px;
  width: 30px;
  vertical-align: middle;
`;

const Actions = styled.div`
  margin-right: 2px;
  margin-top: 2px;
`;

const MenuItemStyle = css`
  padding: 10px 16px;
`;

export interface CompanyDirectorDetailsProps {
  director: Director;
  onRemove: (index: number) => void;
  index: number;
}

function CompanyDirectorDetails({ director, onRemove, index}: CompanyDirectorDetailsProps) {
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

  function onMenuClose() {
    setAnchorEl(null);
  }

  function onMenuRemove() {
    onMenuClose();
    onRemove(index);
  }

  function onMenuRunCreditCheck() {
    onMenuClose();
    // onRemove(index);
  }

  function onMenuClick(evt: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    setAnchorEl(evt.currentTarget);
  }

  return (
    <DirectorRoot>
      <Head>
        <Title>
          <Icon>
            <MdPerson/>
          </Icon>
          <CoreDetails>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Name>{director.firstGivenName} {director.familyName}</Name>
              </Grid>
            </Grid>
          </CoreDetails>
        </Title>
        <Actions>
          <IconButton
            onClick={onMenuClick}
          >
            <MdMoreVert />
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            getContentAnchorEl={null}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={onMenuClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
          >
            <MenuItem className={MenuItemStyle} onClick={onMenuRunCreditCheck}>Run Credit Check</MenuItem>
            <MenuItem className={MenuItemStyle} onClick={onMenuRemove}>Remove</MenuItem>
          </Menu>
        </Actions>
      </Head>
      {
        !!director.appointmentDate &&
        <Body>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <DisplayField
                label="Appointment Date"
                value={formatDate(director.appointmentDate)}
              />
            </Grid>
            {
              !!director.ceaseDate &&
              <Grid item xs={12} md={6}>
                <DisplayField
                  label="Cease Date"
                  value={formatDate(director.ceaseDate)}
                />
              </Grid>
            }
          </Grid>
        </Body>
      }
    </DirectorRoot>
  )
}

export default CompanyDirectorDetails;
